<template>
        <div class="mBg h">
            <div class="pr">
                <div class="uv-i-Head">
                    <div class="ui-nav">
                        <router-link to="/home">
                            <i class="el-icon-s-home icon"></i>
                        </router-link>
                        <div class="ui-navText">
                            个人中心
                        </div>
                        <router-link to="/cart/cart">
                            <i class="el-icon-shopping-cart-1 icon"></i>
                        </router-link>
                    </div>
                </div>
                <div class="m-ui-self">
                    <div class="m-us-box">
<!--                        <div class="">-->
<!--                            <el-avatar icon="el-icon-user-solid" :src="avatar"></el-avatar>-->
<!--                        </div>-->
                        <div class="muc-self" v-if="login">
                            <span class="wto">{{username}}</span>
                        </div>
                        <div class="muc-self" v-else>
                            <router-link to="/login/login">
                                <el-button class="tc" type="primary" size="small" plain>登录/注册</el-button>
                            </router-link>
                        </div>
                    </div>
                </div>
            </div>
            <div class="m-ui-order">
                <div class="mu-tit bbGray" @click="toUrl('/userCenter/order/order')">我的订单</div>
                <div class="mu-order">
                    <div class="mu-o-item" @click="toOrder('weifukuan')">
                        <el-badge :value="orderValue[0]" class="item" :max="10" :hidden="orderValue[0]==0">
                            <img src="../../../images/ucicon/dfk.png">
                            <span>待付款</span>
                        </el-badge>
                    </div>
                    <div class="mu-o-item" @click="toOrder('yifukuan')">
                        <el-badge :value="orderValue[1]" class="item" :max="10" :hidden="orderValue[1]==0">
                            <img src="../../../images/ucicon/dfh.png">
                            <span>待收货</span>
                        </el-badge>
                    </div>
                    <div class="mu-o-item" @click="toOrder('daipinglun')">
                        <el-badge :value="orderValue[2]" class="item" :max="10" :hidden="orderValue[2]==0">
                            <img src="../../../images/ucicon/dpj.png">
                            <span>待评价</span>
                        </el-badge>
                    </div>
                    <div class="mu-o-item" @click="toOrder('quxiao')">
                        <img src="../../../images/ucicon/qxdd.png">
                        <span>已取消</span>
                    </div>
                </div>
            </div>
            <div class="mu-other">
                <div class="mu-o-item" @click="toUrl('/userCenter/order/order')">
                    <img src="../../../images/ucicon/muc-Icon1.png">
                    <span>全部订单</span>
                </div>
                <div class="mu-o-item" @click="toUrl('/userCenter/collect/collect')">
                    <img src="../../../images/ucicon/muc-Icon2.png">
                    <span>我的收藏</span>
                </div>
                <div class="mu-o-item" @click="toUrl('/userCenter/invoice/invoice')">
                    <img src="../../../images/ucicon/muc-Icon3.png">
                    <span>发票管理</span>
                </div>
                <div class="mu-o-item" @click="toUrl('/userCenter/address/address')">
                    <img src="../../../images/ucicon/muc-Icon4.png">
                    <span>地址管理</span>
                </div>
                <div class="mu-o-item" @click="toUrl('/userCenter/message/message')">
                    <img src="../../../images/ucicon/muc-Icon5.png">
                    <span>消息通知</span>
                </div>
                <div class="mu-o-item" @click="toUrl('/userCenter/evaluate/mine')">
                    <img src="../../../images/ucicon/muc-Icon6.png">
                    <span>我的评价</span>
                </div>
                <div class="mu-o-item" @click="callPhone(18676881096)">
                    <img src="../../../images/ucicon/muc-Icon7.png">
                    <span>联系客服</span>
                </div>
                <div class="mu-o-item">
                    <img src="../../../images/ucicon/muc-Icon8.png">
                    <span>关于我们</span>
                </div>
            </div>
            <div class="mu-o-panel">
                <div class="mu-o-line">
                    <div class="mu-o-lf" @click="callPhone(4008383509)">
                        <span>服务热线：<b>400-8383-509</b></span>
                    </div>
                    <div class="mu-o-rg"></div>
                </div>
            </div>
            <div class="mu-o-btn" v-if="login" @click="outLogin()">
                <el-button class="w"  plain>退出登录</el-button>
            </div>
            <nav-foot menuActive="mine"></nav-foot>
        </div>


</template>


<script>
    import navFoot from '../../../components/mobile/navFoot';
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {signout,getMyOrderList} from '../../../service/api';
    export default {
        name: 'ucIndex',
        data(){
            return{
                profiletitle: '我的',
                username: '登录/注册',//用户名
                resetname: '',
                mobile: '暂无绑定手机号',//电话号码
                balance: 0,//我的余额
                count : 0,//优惠券个数
                pointNumber : 0, //积分数
                avatar: '',//头像地址
                order:[],
                orderValue:[]
            }
        },
        components:{
            navFoot
        },
        async beforeMount(){
            this.getUserInfo();
        },
        mounted(){
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','login'
            ]),
        },
        methods:{
            ...mapMutations([
                'SAVE_AVANDER','OUT_LOGIN'
            ]),
            ...mapActions([
                'getUserInfo'
            ]),
            async initData(){

                //订单状态-数量
                var order1 = await getMyOrderList('weifukuan','','');
                this.orderValue[0] = order1.count;

                var order2 = await getMyOrderList('yifukuan','','');
                var order3 = await getMyOrderList('peisong','','');
                this.orderValue[1] = order2.count + order3.count;

                var order4 = await getMyOrderList('daipinglun','','');
                this.orderValue[2] = order4.count;
                console.log(this.orderValue);

                this.avatar = this.userInfo.avatar||'';
                this.username = this.userInfo.username;
                this.mobile = this.userInfo.mobile || '暂无绑定手机号';
                this.balance = '';
                this.count = '';
                this.pointNumber = '';

                this.$forceUpdate();

                if(!this.login){
                    this.OUT_LOGIN();
                    this.username = '登录/注册';
                    this.mobile = '暂无绑定手机号';
                }
            },
            async outLogin(){
                this.$confirm('你确定要退出登录吗?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    var res = await signout();
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.OUT_LOGIN();
                        removeStore('user_id');
                        this.$forceUpdate();
                    }
                }).catch(() => {

                });
            },
            toUrl(url){
                if (this.userInfo && this.userInfo.user_id){
                    this.$router.push({path:url});
                }else{
                    this.$message({
                        message: '请先登录',
                        type: 'warning'
                    });
                }
            },
            toOrder(tar){
                if (this.userInfo && this.userInfo.user_id){
                    this.$router.push({path:"/userCenter/order/order",query:{activeName:tar}});
                }else{
                    this.$message({
                        message: '请先登录',
                        type: 'warning'
                    });
                }
            },
            callPhone (phoneNumber) {
                window.location.href = 'tel://' + phoneNumber
            }

        },
        watch:{
            login: function (value){
                //监听登录状态，退出登录后，页面刷新
                this.initData();
            }
        },
        created(){
            //this.username = this.userInfo.username

        }

    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    body{
        height: 100%;
        background-color:$bggray;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        min-height: px2rem(1140);
        height: 100%;
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .uv-i-Head{
        width:100%;
        height: px2rem(180);
        position: fixed;
        top:0;
        left:0;
        background-color:$default;
        z-index: 1000;
        line-height: px2rem(90);
        color:#fff;
        text-align: center;
        z-index:0;
    }
    .ui-nav{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .ui-nav .icon{
        font-size:px2rem(52);
        line-height:px2rem(90);
        padding:0 px2rem(20);
    }
    .ui-navText{
        height: px2rem(90);
        line-height: px2rem(90);
        font-size: px2rem(36);
    }

    .m-ui-self{
        position: absolute;
        top:px2rem(0);
        left: px2rem(20);
        width:px2rem(710);
        height: px2rem(106);
        background-color: #fff;
        margin-bottom:px2rem(20);
        border-radius:px2rem(10);
        z-index: 2000;
    }
    .m-us-box{
        padding:px2rem(20);
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;
    }
    .muc-self{
        line-height: px2rem(60);
        font-size: px2rem(36);
    }

    /*我的订单*/
    .m-ui-order{
        padding:0 px2rem(20);
        background-color: #fff;
        border-radius:px2rem(10);
        margin:px2rem(130) px2rem(20) px2rem(20) px2rem(20);
        z-index:10;
    }
    .mu-tit{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        height: px2rem(84);
        line-height: px2rem(84);
    }
    .mu-order{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        z-index:2;
    }
    .mu-o-item{
        text-align: center;
        padding:px2rem(20) 0;
        width:px2rem(160);
        display: inline-block;
    }
    .mu-o-item img{
        width:px2rem(52);
        height: px2rem(52);
        display: block;
        margin:0 auto;
    }
    .mu-o-item span{
        display: block;
        text-align:center;
        font-size:px2rem(26);
    }

    /*其他*/
    .mu-other{
        padding:px2rem(20) ;
        background-color: #fff;
        border-radius:px2rem(10);
        margin: px2rem(20);
        z-index:2;
    }
    .mu-other img{
        width:px2rem(66);
        height: px2rem(66);
    }
    .mu-o-panel{
        padding:px2rem(20) ;
        background-color: #fff;
        border-radius:px2rem(10);
        margin: px2rem(20);
        z-index:2;
    }

    /*退出按钮*/
    .mu-o-btn{
        margin: px2rem(20);
        z-index:2;
    }


</style>



